* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  width: 100vw;
}

.background {
  background-color: #1c1b2b;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  padding: 30px;
}

.main {
  background-color: #1f2542;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  border-radius: 14px;
  flex: 1;
}

header {
  display: flex;
  flex-direction: column;
  width: 220px;
  height: 100%;
  color: #f7cace;
}

.headerList {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin: 0;
  padding: 8px 6px;
  gap: 8px;
  background-color: #f5f5f5;
  flex: 1;
  border-bottom-left-radius: 14px;
  border-bottom-right-radius: 14px;
}

.headerItemList {
  padding: 4px 18px;
  border-radius: 10px;
}

.headerItemList span {
  color: #fff;
}

.headerItemList:hover {
  /*background-color: #DAD2FF;*/
  cursor: pointer;
}

.contentContainer {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
  padding: 25px 0;
}

button:hover {
  cursor: pointer;
}

input,
select {
  outline: none;
}
